import uniPopup from "../components/uni-popup/uni-popup.vue"
export default{
  components: {
    uniPopup
  },
  data() {
    return {
      checked: true,
      page_isBindTel: false,             // 默认是登录，false为绑定手机号
      mobile: 18888888888,                        // 微信手机号码
    }
  },
  methods: {
    
    // 用户协议勾选
    checkboxChange(e) {
      let that= this
      that.checked= e.detail.value.length > 0? true: false
    },
    
    // 获取微信步数授权
    getWechatFoot() {
      console.log('调用了微信步数')
      let that= this
      uni.login({
        provider: 'weixin',
        success: function (loginRes) {
          // 记录步数
          let encryptedData= uni.getStorageSync('encryptedData'),
            iv= uni.getStorageSync('iv'),
            data= {
              url: that.$api.setpCount,
              method: 'POST',
              data: {
                encryptedData: encryptedData,
                iv: iv,
                code: loginRes.code
              }
            }
          that.$httpRequest(data).then(res => {
            console.log(res)
          })
        }
      })
    },
    
    
    // 登录
    authorLogin(e) {
      let that= this
      // 同意用户协议
      if(!that.checked) {
        uni.showToast({
          title: '请勾选相关用户协议',
          icon: 'none'
        })
        return
      }
      let params= {
          url: that.$api.login,
          method: 'POST',
          data: {
            code: '',
            user_info: e.detail.rawData,
            encrypted_data: e.detail.encryptedData,
            iv: e.detail.iv,
            signature: e.detail.signature,
            referee_id: wx.getStorageSync('referee_id')
          }
        }
      // 调起登录
      uni.login({
        provider: 'weixin',
        success: function (loginRes) {
          console.log(loginRes)
          uni.showLoading({
              title: '登录中'
          })
          params.data.code= loginRes.code
          
          // 请求登录接口
          that.$httpRequest(params).then(res => {
            
            uni.hideLoading()
            that.$store.commit('setToken', res.data.token)
            that.$store.commit('redirectLoginPage', {status: false})
            uni.setStorageSync('token', res.data.token)
            uni.setStorageSync('user_id', res.data.user_id)
            uni.setStorageSync('is_merchant', res.data.is_merchant)
            
            let isBindPhone= res.data.mobile_isbind === 'yes'? true: false
            that.getWechatFoot()
            
            // 已经绑定手机号码
            if(isBindPhone) {
              // 判断是否是商家
              if(res.data.is_merchant > 0) {
                // 是商家
                uni.reLaunch({
                  url: '/pages/partner/index'
                })
              } else {
                uni.navigateBack({
                  delta: 1
                })  
              }
            } else {
              that.page_isBindTel= true
            }
          })
        }
      })
      
    },
    reject() {
      this.$store.commit('redirectLoginPage', {status: false})
      uni.switchTab({
        url: '../index/index'
      })
    },
    
    // 解密获取手机号码
    getPhoneNumber(e) {
      console.log('获取到手机号码', e)
      let that= this,
        params= {}
      
      that.open()
      
      // uni.showLoading({
      //   title: '授权获取中'
      // })
      // 获取微信步数授权
      // uni.login({
      //   provider: 'weixin',
      //   success: function (res) {
      //     console.log(res)
      //       params= {
      //         url: that.$api.decryptByMobile,
      //         method: 'POST',
      //         data: {
      //           code: res.code,
      //           encryptedData: e.detail.encryptedData,
      //           iv: e.detail.iv
      //         }
      //       }
      //     that.$httpRequest(params).then(result => {
      //       // uni.hideLoading()
      //       that.mobile= result.data.mobile
      //       that.open()
      //     })
      //   }
      // })
      
    },
    
    // 绑定手机号码
    bindWechatTel() {
      let that= this,
        params= {
          url: that.$api.mobileBind,
          method: 'POST',
          data: {
            mobile: 15979779714
          }
        }
      that.$httpRequest(params).then(res => {
        console.log(res)
        uni.showToast({
          title: res.msg,
          success: () => {
            if(res.data.is_merchant > 0) {
              uni.redirectTo({
                url: '/pages/partner/index'
              })
            } else {
              uni.navigateBack({
                delta: 1
              })
            }
          }
        })
      })
    },
    
    // 打开弹窗
    open(){
      // 需要在 popup 组件，指定 ref 为 popup
      this.$refs.popup.open()
    },
    close() {
      this.$refs.popup.close()
    }
  }
}
